<template>
<div class="content-area">
     <div class="login-div">
         <div>
             <!--  -->
             <div class="login-left-content">
                <h2>hello </h2>
                <p>  ?
                 !
                    ,       .
                </p>
             </div>
         </div>
         <div>
            <div class="login-content">
             <form name="loginForm" action="/login" method="post">
                  <div class="input">
                    <input type="text" data-y="dd" name="user_id" id="user_id" v-model="loginObj.user_id" placeholder="">
                  </div>
                   <div class="input">
                    <input type="password" name="user_pwd" id="user_pwd" v-model="loginObj.user_pwd" placeholder="">
                  </div>

                  <div>
                  <input @click="joinPage" type="button"  value="">
                  <input @click="userLogin" type="button" value="">
                  </div>
             </form>
            </div>
         </div>
     </div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { commonUtil } from '@/jsUtil/commonUtil.js'

const userStore = 'userStore'

export default {
  name: 'login',
  data () {
    return {
      loginObj: {
        user_id: '',
        user_pwd: ''
      }
    }
  },
  computed: {
    ...mapGetters(userStore, {checkLogin: 'GET_LOGIN_STATE'}),
    watchLoginState () { return this.checkLogin }
  },
  methods: {
    ...mapActions(userStore, {login: 'AC_USER_LOGIN'}),
    joinPage: function () {
      this.$router.push({name: 'joinPage'})
    },
    userLogin: function (event) {
      event.preventDefault()
      if (this.loginObj.user_id === '' || this.loginObj.user_pwd === '') {
        alert('  .')
        return
      }
      let form = commonUtil.ConvertForm(this.loginObj)
      this.login(form)
    }
  },
  watch: {
    watchLoginState (newVal, oldVal) {
      if (newVal === 'true') {
        alert(' ')
        this.$router.push({name: 'main'})
      }
    }
  }
}
</script>

<style scoped>
.login-div{
  overflow: hidden;
  border-radius: 10px;
  display: flex;
  flex-wrap: nowrap;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px;
}

.login-div > div{
  flex-basis: 394px;
  min-height: 500px;
}
.login-div > div:first-child{
   position: relative;
    background-color: #3CAEFF;
    position: relative;
}
.login-div > div:first-child  .login-left-content{
    position: absolute;
    top: 148px;
    text-align: center;
    right: 35px;
    color: white;
    font-weight: bold;
    width: 81%;
}

.login-left-content p{
  font-size: 15px;
  color:white;
  opacity: 0.8;
}

.login-content{
  margin: 178px 0px 30px 0px;
}

.input input{
    border: 0;
    border-bottom-style: solid;
    border-width: 1px;
    height: 31px;
    width: 212px;
}
.input input:focus{
    outline: 0;
}

.content-area{
  margin-top: 100px;
  width: 800px;
  margin: 0 auto;
}
@media (max-width: 750px){
  .content-area{
    width:100%;
  }
}

@media (max-width: 750px) {
    .login-div{
      flex-direction:column
    }
    .login-div > div{
      width: auto;
      height: 200px;
      flex-grow: 1;
    }
    .login-left-content{
      width: auto;
    }
}
</style>



