import Vue from 'vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faSignOutAlt, faSignInAlt, faLink, faBold, faUnderline, faItalic, faCode, faFileCode, faParagraph, faListUl, faListOl, faQuoteLeft, faWindowMinimize, faUndo, faRedo } from '@fortawesome/free-solid-svg-icons'

/*           . */
library.add(faSignOutAlt, faSignInAlt, faLink, faBold, faUnderline, faItalic, faCode, faFileCode, faParagraph, faListOl, faListUl, faQuoteLeft, faWindowMinimize, faUndo, faRedo)

/*     */
Vue.component('f-icon', FontAwesomeIcon)



